<script>
import api from '@/api'
import { getToken } from '@/api/auth'
import Editor from '@/components/Editor'

export default {
  inject: ['reload'],
  data () {
    return {
      formValidate: {
        id: '',
        name: '',
        fgame: '',
        title: '',
        state: '',
        original: '',
        keywords: '',
        description: '',
        game_score: '',
        platname: '',
        language: '',
        safetitle: [],
        common_id: '',
        and_ver: '',
        and_size: '',
        and_unit: '',
        and_url: '',
        and_lock: '',
        cooperation: '',
        is_down: '',
        ios_ver: '',
        ios_size: '',
        ios_unit: '',
        ios_url: '',
        pc_url: '',
        type: '',
        recom: '',
        fictitious_down: '',
        comment: '',
        tip: '',
        is_yuyue: '',
        p_permission: '',
        tags: '',
        check_updateTime: '0',
        icon: '',
        introduce: '',
        journal: [],
        gameImageIds: '',
        source_id: '0',
        source_url: ''
      },
      formArr: {
        ag_language: [],
        game_recom: [],
        game_type: [],
        pack_unit: [],
        safetitleList: [],
        tags: [],
        gameImageList: []
      },
      visible: false,
      discussArr: ['','','','',''],
      reviewType: 1,
      curVideoCoverUrl: '',
      curIconUrl: '',
      preImgHref: '',
      remark: '',
      curName: '游戏',
      tagsLimitNum: 8,
    }
  },
  created () {
    this.reviewType = this.$route.query.type;
    this.curName = this.$route.query.type == 1 ? '游戏' : '应用';
    this.tagsLimitNum = this.$route.query.type == 1 ? 8 : 4;
    const path = this.$route.query.type == 1 ? 'editReviewGame' : 'editReviewApp',
          token = getToken(),
          type_c = 2,
          id = this.$route.query.id,
          params = {token, type_c, id};

    api[path](params).then(res => {
      if(res.code == 0) {
        // console.log(res)
        const {id='', name, fgame, title, state, original, keywords, description, game_score, platname='', language, safetitle, common_id, is_down, type, recom, fictitious_down, comment, cooperation, tip, discuss, is_yuyue, p_permission, tags, icon, introduce, journal, video, video_cover, source_id, source_url,status} = res.data.gameInfo;
        const packInfo = res.data.packInfo || {};
        const {and_ver='', and_size='', and_unit='1', and_url='', and_lock='', ios_ver='', ios_size='', ios_unit='1', ios_url='', pc_url='', } = packInfo;
        let {ag_language, game_recom, game_type, pack_unit, app_type, app_source} = res.data.config;
        let gameImageList = res.data.img;
        this.curIconUrl = icon;
        this.curVideoCoverUrl = video_cover;
        this.remark = status == 3 ? res.data.review ? res.data.review.remark : '' : '';
        const safeTitleArr = safetitle ?  safetitle.split(',') : [];
        this.formValidate =  {
          id,
          name,
          fgame,
          title,
          state,
          original,
          keywords,
          description,
          game_score,
          platname,
          language,
          safetitle: safeTitleArr,
          common_id,
          and_ver,
          and_size,
          and_unit,
          and_url,
          and_lock,
          cooperation,
          is_down,
          ios_ver,
          ios_size,
          ios_unit,
          ios_url,
          pc_url,
          type,
          recom,
          fictitious_down,
          comment,
          tip,
          is_yuyue,
          p_permission,
          tags,
          check_updateTime: '0',
          icon,
          introduce,
          journal,
          video,
          source_id,
          source_url
        };//表单基础数据传值
        // console.log(this.formValidate)
        this.formArr = {
          ag_language: ag_language,
          game_recom: game_recom,
          game_type: game_type || app_type,
          pack_unit: pack_unit,
          safetitleList: res.data.config.safetitle,
          tags: res.data.tagInfo,
          gameImageList,
          app_source
        };//列数据传值

        if(discuss && discuss.length>0) {
          discuss.forEach((ele,idx) => {
            this.discussArr[idx] = ele
          })
        }//评论传值
        this.$refs.editorConfig.editor1.disable()
        this.$refs.editorDataConfig.editor2.disable()
        this.$refs.editorDataHistoryConfig.editor2.disable()
      } else {
        this.$Notice.error({
          desc: res.msg
        })
      }
    })
  },
  methods: {
    returnManuscript () {
      if(this.remark.trim() != '') {
        this.$Spin.show();
        const path = this.reviewType == 1 ? 'editReviewGame' : 'editReviewApp',
              backPath = this.reviewType == 1 ? '/review/reviewGame' : '/review/reviewApp',
              token = getToken(),
              type_c = 1,
              id = this.$route.query.id*1,
              remark = this.remark,
              params = {token, type_c, id, remark};

        console.log(params, path, backPath)   
        api[path](params).then(res => {
          this.$Spin.hide();
          if(res.code == 0) {
            this.$Notice.success({
              title: '退稿成功'
            })
            this.$router.push({path: backPath})
          } else {
            this.$router.push({path: backPath})
            this.$Notice.error({
              title: res.msg
            })
          }
        })
      } else {
        this.$Message.warning('请输入退稿原因')
      }
    },
    reviewSuccess () {
      this.$Spin.show();
      const backPath = this.reviewType == 1 ? '/review/reviewGame' : '/review/reviewApp',
            token = getToken(),
            type = this.reviewType,
            id = this.$route.query.id,
            params = {token, type, id};

      api.approved(params).then(res => {
        this.$Spin.hide();
        if(res.code == 0) {
          this.$router.push({path: backPath})
          this.$Notice.success({
            title: res.msg
          })
        } else {
          this.$router.push({path: backPath})
          this.$Notice.error({
            title: res.msg
          })
        }
      })
    },
    handleView (url) {
      this.preImgHref = url;
      this.visible = true;
    },//截图预览
  },
  components: {Editor}
}
</script>
<template>
<div>
  <h3 v-if="reviewType == 1"><Icon type="ios-boat" size=24 />审核游戏</h3>
  <h3 v-else><Icon type="ios-boat-outline" size=24 />审核应用</h3>
  <Form ref="formValidate" :model="formValidate" :label-width="130">
    <div class="formBox highLight">
      <FormItem label="退稿原因" prop="remark" class="big">
        <Input v-model="remark" />
      </FormItem>
      <Button type="warning" style="height:33px;margin-left:10px" @click="returnManuscript">退回稿件</Button>
    </div>
    <div class="formBox" v-if="reviewType == 2">
      <FormItem label="来源" prop="state">
        <Select v-model="formValidate.source_id" disabled>
          <Option value="0">选择来源</Option>
          <Option v-for="itm,idx in formArr.app_source" :value="idx" :key="idx">{{itm}}</Option>
        </Select>
      </FormItem>
      <FormItem label="来源URL" prop="source_url" class="default">
        <Input v-model="formValidate.source_url" placeholder="http:// 或 https://开头" disabled></Input>
      </FormItem>
    </div>
    <div class="formBox">
      <FormItem :label="curName+'名称'" prop="name" class="small nameBox">
        <Input v-model="formValidate.name" disabled />
      </FormItem>
      <FormItem :label="'主库'+curName" prop="fgame" class="fgameBox">
          <Input v-model="formValidate.fgame" disabled />
      </FormItem>
      <FormItem label="是否原创" prop="original">
        <Select v-model="formValidate.original" disabled>
          <Option value="1">是</Option>
          <Option value="2">否</Option>
        </Select>
      </FormItem>
      <FormItem :label="curName+'状态'" prop="state">
        <Select v-model="formValidate.state" disabled>
          <Option value="1">正常</Option>
          <Option value="2">下架</Option>
          <Option value="3">删除</Option>
        </Select>
      </FormItem>
    </div>
    <FormItem label="网页SEO标题" prop="title" class="default">
      <Input v-model="formValidate.title" disabled></Input>
    </FormItem>
    <FormItem label="网页SEO关键词" prop="keywords" class="default">
      <Input v-model="formValidate.keywords" disabled ></Input>
    </FormItem>
    <FormItem label="网页SEO描述" prop="description" class="big">
      <Input v-model="formValidate.description" type="textarea" :autosize="{minRows: 4,maxRows: 14}" disabled></Input>
    </FormItem>
    <FormItem :label="curName+'评分'" prop="game_score" class="small">
      <Input v-model="formValidate.game_score" disabled ></Input>
    </FormItem>
    <div class="formBox">
      <FormItem :label="curName+'厂商'" prop="platname" class="default">
        <Input v-model="formValidate.platname" disabled ></Input>
      </FormItem>
      <FormItem label="语言类型" prop="language">
        <Select v-model="formValidate.language" disabled>
          <Option v-for="itm,idx in formArr.ag_language" :value="idx" :key="idx">{{itm}}</Option>
        </Select>
      </FormItem>
    </div>
    <FormItem label="安全提示">
      <CheckboxGroup v-model="formValidate.safetitle">
        <Checkbox v-for="itm,idx in formArr.safetitleList" :label="idx" :key="idx" disabled>{{itm}}</Checkbox>
      </CheckboxGroup>
    </FormItem>
    <div class="formBox">
      <FormItem label="安卓版本号" prop="and_ver"  class="small">
        <Input v-model="formValidate.and_ver" disabled ></Input>
      </FormItem>
      <FormItem label="安卓包大小" prop="and_size"  class="small">
        <Input v-model="formValidate.and_size" disabled ></Input>
      </FormItem>
      <Select v-model="formValidate.and_unit" style="marginLeft:5px;width:80px" disabled>
        <Option v-for="itm,idx in formArr.pack_unit" :value="idx" :key="idx">{{itm}}</Option>
      </Select>
      <FormItem label="是否合作" prop="cooperation">
        <Select v-model="formValidate.cooperation" disabled>
          <Option value="1">是</Option>
          <Option value="2">否</Option>
        </Select>
      </FormItem>
    </div>
    <div class="formBox">
      <FormItem label="安卓下载地址" prop="and_url" class="default">
        <Input v-model="formValidate.and_url" disabled ></Input>
      </FormItem>
      <Button type="info" style="height:32px;margin-left:10px">M地址</Button>
    </div>
    <div class="formBox">
      <FormItem label="ios版本号" prop="ios_ver" class="small">
        <Input v-model="formValidate.ios_ver" disabled ></Input>
      </FormItem>
      <FormItem label="ios包大小" prop="ios_size" class="small">
        <Input v-model="formValidate.ios_size" disabled ></Input>
      </FormItem>
      <Select v-model="formValidate.ios_unit" style="marginLeft:5px;width:80px" disabled>
        <Option v-for="itm,idx in formArr.pack_unit" :value="idx" :key="idx">{{itm}}</Option>
      </Select>
    </div>
    <FormItem label="ios下载地址" prop="ios_url" class="big">
      <Input v-model="formValidate.ios_url" disabled ></Input>
    </FormItem>
    <FormItem label="pc下载地址" prop="pc_url" class="big">
      <Input v-model="formValidate.pc_url" disabled ></Input>
    </FormItem>
    <FormItem :label="curName+'类型'" prop="type">
      <RadioGroup v-model="formValidate.type">
        <Radio v-for="itm, idx in formArr.game_type" :label="idx" :key="idx" disabled>{{itm}}</Radio>
      </RadioGroup>
    </FormItem>
    <div class="formBox">
      <FormItem label="推荐设置" prop="recom">
        <Select v-model="formValidate.recom" disabled>
          <Option v-for="itm, idx in formArr.game_recom" :value="idx" :key="idx">{{itm}}</Option>
        </Select>
      </FormItem>
      <FormItem label="下载数量" prop="fictitious_down">
        <Input v-model="formValidate.fictitious_down" disabled ></Input>
      </FormItem>
    </div>
    <FormItem :label="curName+'内容介绍'" prop="introduce">
      <Editor 
        :getEditorContent="formValidate.introduce"
        :getEditorType="1"
        :getEditorId="'editorContainer_1'"
        ref="editorConfig"
        />
    </FormItem>
    <FormItem label="日志">
      <Editor 
        :getEditorContent="formValidate.journal ? formValidate.journal[0] : ''"
        :getEditorType="2"
        :getEditorId="'editorContainer_2'"
        ref="editorDataConfig"
        />
    </FormItem>
    <FormItem label="历史日志">
      <Editor 
        :getEditorContent="formValidate.journal ? formValidate.journal[1] : ''"
        :getEditorType="2"
        :getEditorId="'editorContainer_3'"
        ref="editorDataHistoryConfig"
        />
    </FormItem>
    <FormItem label="小编推荐" prop="comment" class="big">
      <Input v-model="formValidate.comment" type="textarea" :autosize="{minRows: 4,maxRows: 15}" disabled></Input>
    </FormItem>
    <FormItem label="重要提示" prop="tip" class="big">
      <Input v-model="formValidate.tip" type="textarea" :autosize="{minRows: 4,maxRows: 15}" disabled></Input>
    </FormItem>
    <FormItem label="评论" prop="discuss">
      <Input
        v-model="discussArr[0]"
        :value="discussArr[0]" 
        :maxlength=300
        show-word-limit
        type="textarea"
        :rows="4"
        style="margin: 0 6px 6px 0;width:19%"
        placeholder="评论不能字数超过300" disabled />
      <Input
        v-model="discussArr[1]"
        :value="discussArr[1]" 
        :maxlength=300
        show-word-limit
        type="textarea"
        :rows="4"
        style="margin: 0 6px 6px 0;width:19%" 
        placeholder="评论不能字数超过300" disabled />
      <Input
        v-model="discussArr[2]"
        :value="discussArr[2]" 
        :maxlength=300
        show-word-limit
        type="textarea"
        :rows="4"
        style="margin: 0 6px 6px 0;width:19%"
        placeholder="评论不能字数超过300" disabled />
      <Input
        v-model="discussArr[3]"
        :value="discussArr[3]" 
        :maxlength=300
        show-word-limit
        type="textarea"
        :rows="4"
        style="margin: 0 6px 6px 0;width:19%"
        placeholder="评论不能字数超过300" disabled />
      <Input
        v-model="discussArr[4]"
        :value="discussArr[4]" 
        :maxlength=300
        show-word-limit
        type="textarea"
        :rows="4"
        style="margin: 0 6px 6px 0;width:19%"
        placeholder="评论不能字数超过300" disabled />
    </FormItem>
    <FormItem label="是否预约" prop="is_yuyue">
      <RadioGroup v-model="formValidate.is_yuyue">
        <Radio label="1" disabled>是</Radio>
        <Radio label="0" disabled>否</Radio>
      </RadioGroup>
    </FormItem>
    <FormItem label="权限提示层" prop="p_permission" class="default">
      <Input v-model="formValidate.p_permission" type="textarea" :autosize="{minRows: 4,maxRows: 15}" disabled></Input>
    </FormItem>
    <FormItem label="关联标签" prop="tags">
      <template v-if="formArr.tags&&formArr.tags.length>0">
        <Tag v-for="item,idx in formArr.tags" v-show="idx<tagsLimitNum" :key="idx" :name="item.name">{{item.name}}</Tag>
      </template>
      <template v-else>
        <Tag v-for="item,idx in ['','','','','','','','']" v-show="idx<tagsLimitNum" :key="idx" :name="item.name" style="width:60px">{{item.name}}</Tag>
      </template>
    </FormItem>
    <FormItem label="视频地址" class="big">
      <Input v-model="formValidate.video" type="textarea" :rows=4 disabled ></Input>
    </FormItem>
    <div class="formBox">
      <FormItem label="icon图片(160x160)" prop="gimg">
        <img class="defaultImg" :src="curIconUrl" alt="">
      </FormItem>
      <FormItem label="视频封面图">
        <img class="defaultImg" :src="curVideoCoverUrl" alt="">
      </FormItem>
    </div>
    <FormItem :label="curName+'截图'">
      <div class="uploadlistBox" v-if="formArr.gameImageList.length>0">
        <div class="upload-list" v-for="item,idx in formArr.gameImageList" :key="idx">
          <img :src="item" title="item">
          <div class="upload-list-cover">
            <Icon type="ios-eye-outline" @click.native="handleView(item)"></Icon>
          </div>
        </div>
      </div>
      <div class="noImg" v-else>
        <Icon type="ios-images-outline" size="52"></Icon>
        <p>暂无截图</p>
      </div>
      <Modal title="图片预览" v-model="visible">
        <img :src="preImgHref" v-if="visible" style="width: 100%;max-height:600px">
        <p slot="footer" style="textAlign:center;overflow: hidden;">截图预览</p>
      </Modal>
    </FormItem>
    <FormItem>
      <Button type="primary" @click="reviewSuccess">审核通过</Button>
    </FormItem>
  </Form>
  <BackTop></BackTop>
</div>
</template>
<style>
.ivu-input[disabled], .ivu-select-disabled .ivu-select-selection {
  color: #515a6e;
}
.ivu-radio-disabled .ivu-radio-inner:after {
  background-color: #2d8cf0;
}
.highLight label {
  color: red!important;;
}
</style>
<style lang="scss" scoped>
  .small {
    width: 25%;
  }
  .default {
    width: 40%;
  }
  .big {
    width: 50%;
  }
  .formBox {
    display: flex;
    .nameBox {
      margin-right: 66px;
      span {
        width: 110px;
        position: absolute;
        color: green;
        margin-left: 6px;
        &.err1 {
          color: red;
        }
      }
    }
    .fgameBox {
      margin-right: 66px;
      ul {
        min-width: 180px;
        max-height: 200px;
        position: absolute;
        will-change: top, left;
        transform-origin: center top;
        top: 33px;
        left: 0px;
        list-style: none;
        overflow: auto;
        margin: 5px 0;
        padding: 5px 0;
        background-color: #fff;
        box-sizing: border-box;
        border-radius: 4px;
        box-shadow: 0 1px 6px #00000020;
        z-index: 900;
        li {
          width: 180px;
          line-height: 32px;
          text-indent: 8px;
          margin: 0;
          clear: both;
          color: #515a6e;
          font-size: 12px!important;
          white-space: nowrap;
          list-style: none;
          cursor: pointer;
          transition: all .2s ease-in-out;
          &:hover {
            background: #dedede99;
          }
        }
      }
      span {
        width: 110px;
        position: absolute;
        color: green;
        margin-left: 6px;
        &.err2 {
          color: red;
        }
      }
      .addGame {
        position: absolute;
        margin-left: 100px;
      }
    }
  }
  .uploadBox {
      display: inline-block;
      width: 100px;
      height: 34px;
      border-radius: 4px;
      border: 1px dashed #ccc;
      position: relative;
      input {
        opacity: 0;
        width: 100px;
        height: 32px;
      }
      .upload {
        position: absolute;
        top: 0;
        left: 0;
        width: 100px;
        padding: 0 4px 0 12px;
      }
  }
  .defaultImg {
    max-height: 160px;
    border: 1px dashed #ccc;
  }
  .uploadlistBox{
    background: #fff;
    border: 1px dashed #dcdee2;
    border-radius: 4px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    transition: border-color .2s ease;
    padding: 20px 20px 20px;
    &:hover {
      border: 1px dashed #2d8cf0;
    }
    .upload-list{
      display: inline-block;
      height: 200px;
      line-height: 200px;
      border-radius: 4px;
      overflow: hidden;
      position: relative;
      margin-right: 6px;
      text-align: center;
      box-shadow: 0 1px 1px #00000020;
      &:hover .upload-list-cover {
        display: block;
      }
      img{
        width: 100%;
        height: 100%;
      }
      .upload-list-cover{
        display: none;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: rgba(0,0,0,.6);
        i{
          color: #fff;
          font-size: 20px;
          cursor: pointer;
          margin: 0 2px;
        }
      }
    }
    .uploadedIcon, .uploadedIconError {
      position: absolute;
      bottom: 2px;
      right: 6px;
      font-size: 26px;
      color: #fff;
      z-index: 1;
      &::after {
        content: '';
        position: absolute;
        top: -32px;
        left: -28px;
        border-width: 30px;
        border-style: solid;
        border-radius: 0 0 4px 0;
        border-color: transparent green green transparent;
        z-index: -1;
      }
    }
    .uploadedIconError {
      &::after {
        border-color: transparent red red transparent;
      }
    }
    .processBox {
      position: absolute;
      top: 0;
      left: 0;
      background: #0009;
      padding: 2px;
    }
  }
  .noImg {
    border: 1px dashed #ccc;
    padding: 20px 0px;
    border-radius: 3px;
    text-align: center;
    padding: 20px 0;
    i {
      color: #3399ff
    }
  }
  .tagList {
    margin: 10px 0 0;
    border: 1px solid #ccc;
    border-radius: 4px;
    span {
      display: inline-block;
      text-align: center;
      padding: 15px;
      cursor: pointer;
      &:hover {
        color: #2db7f5;
      }
    }
  }
  .tagListEmpty {
    margin: 10px 0 0;
    border: 1px solid #ccc;
    border-radius: 4px;
    height: 200px;
    line-height: 200px;
    text-align: center;
    font-size: 17px;
    i {
      position: relative;
      top: -4px
    }
  }
  .videoDuration {
    display: flex;
    width: 12%;
  }
  .mp4Box {
    margin: 140px 0;
    .uploadBox, i.mp4Name {
      vertical-align: top;
      margin-right: 4px;
    }
    video {
      position: absolute;
      top: -70px;
      right: 0;
      max-width: 360px;
      max-height: 200px;
      border: 1px dashed #ccc;
    }
  }
</style>

